import React, { useState, useRef } from 'react';
import styles from './style.module.sass';
import {NavLink} from 'react-router-dom';

const Filter : React.FC = () =>{
    const [translate,setTranslate] = useState(0);
    const [prevTouch,setPrev] = useState([0,0])
    const wrapper = useRef(null as null | HTMLDivElement);
    const nav = useRef(null as null | HTMLElement);

    const handleTouchMove : React.TouchEventHandler<HTMLDivElement> = (e) => {
        const {clientX,clientY} = e.touches[0];
        if(prevTouch[0] !== 0){
            const nextStep = translate - prevTouch[0]+clientX;
            const scroll =  (nav.current?.scrollWidth as number) - (wrapper.current?.clientWidth as number);
            if(Math.abs(nextStep) > scroll || nextStep > 0) {
                if(nextStep>0 && translate !==0) {
                    setTranslate(0);
                } else if(Math.abs(nextStep) > scroll) {
                    setTranslate(-scroll);
                }
                return
            }
            setTranslate(nextStep);
        }
        setPrev([clientX,clientY]);
    }

    return (
        <div ref={wrapper} onTouchMove={handleTouchMove} className={styles.wrapper}>
            <nav ref={nav} style={{transform: `translateX(${translate}px)`}}>
                <NavLink to="/shit" className={styles.element} activeClassName={styles.active}>
                    23 февраля
                </NavLink>
                <NavLink to="/shit2" className={styles.element} activeClassName={styles.active}>
                    Скидки
                </NavLink>
                <NavLink to="/shit3" className={styles.element} activeClassName={styles.active}>
                    Книги
                </NavLink>
                <NavLink to="/shit4" className={styles.element} activeClassName={styles.active}>
                    Электроника
                </NavLink>
                <NavLink to="/shit5" className={styles.element} activeClassName={styles.active}>
                    Весна
                </NavLink>
                <NavLink to="/shit6" className={styles.element} activeClassName={styles.active}>
                    Интерьер
                </NavLink>
                <NavLink to="/shit7" className={styles.element} activeClassName={styles.active}>
                    Ноутбуки
                </NavLink>
                <NavLink to="/shit8" className={styles.element} activeClassName={styles.active}>
                    Хабы
                </NavLink>
                <NavLink to="/shit9" className={styles.element} activeClassName={styles.active}>
                    Катриджы
                </NavLink>
            </nav>
        </div>
    )
}

export default Filter;